.navbar{
    background-color: #060b26;
    height:80px;
    display:flex;
    justify-content:start;
    align-items:center;
}
.menu-bars{
    margin-left:2rem;
    font-size:2rem;
    background:none;
}
.nav-menu{
    background-color: #060b26;
    height:100vh;
    width:250px;
    display:flex;
    justify-content:start;
    position:fixed;
    top:0;
    left:-100%;
    transition:850ms;
}
.nav-text{
    display:flex;
    justify-content:start;
    align-items: center;
    padding:8px 0px 8px 16px;
    top:0;
    list-style:none;
    height:60px;
}
.nav-text a{
    color: #050505;
    display:flex;
    align-items: center;
    padding:0px 16px;
    border-radius:4px;
    font-size:18px;
    height:100%;
    width:95%;
    text-decoration: none;
}
.nav-text a:hover{
background-color: #1a83ff;
}
.nav-menu-items{
    width:100%;
}
.nav-menu.active{
    left:0;
    transition: 350ms;
}